@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cinzel+Decorative&display=swap);
.About {
  background-color: #ffffff;
  color: #d5b970;
  font-family: 'Cinzel Decorative';
}

.About-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
}

.About-border {
  /* border-style: solid; */
  /* border-color: #d5b970; */
  /* border-width: thin; */
  margin-left: 5%;
  margin-right: 5%;
  margin-bottom: 50px;
  max-width: 350px;
  min-height: calc(100% - 60px);
}

.About-paragraph {
  font-family: 'EB Garamond';
  text-justify: inter-word;
  line-height: 1.6;
  margin: 10px;
}

.Contact {
  background-color: #d5b970;
  font-family: 'Cinzel Decorative';
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #ffffff;
}

.Contact-body {
  align-items: center;
  justify-content: center;
  font-family: 'Cinzel Decorative';
  font-size: calc(15px + 2vmin);
  font-weight: 100;
  letter-spacing: 10;
  margin-bottom: 50px;
}

.Jobs {
  background-color: #ffffff;
  color: #d5b970;
  font-family: 'Cinzel Decorative';
  margin-bottom: 50px;
}

.Jobs-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
}

.Jobs-paragraph {
  font-family: 'EB Garamond';
  text-justify: inter-word;
  line-height: 1.6;
  margin: 10px;
  margin-left: 60px;
  margin-right: 60px;
  font-size: calc(8px + 2vmin);
  max-width: 700px;
}

.Jobs-image {
  height: 40vmin;
  pointer-events: none;
}

.PageNotFound {
  text-align: center;
}

.PageNotFound-header {
  background-color: #d5b970;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
}

.PageNotFound-logo {
  height: 40vmin;
  pointer-events: none;
}

.Services {
  background-color: #d5b970;
  color: #ffffff;
  font-family: 'EB Garamond';
  margin-bottom: 50px;
}

.Services-header {
  font-family: 'Cinzel Decorative';
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: top;
  font-size: calc(10px + 2vmin);
}

.Services-paragraph {
  text-justify: inter-word;
  line-height: 1.6;
  margin: 10px;
  margin-left: 50px;
  margin-right: 50px;
  width: 400px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

h1 {
  font-size: calc(25px + 6vmin);
}

